import React from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";


const Navbar = () => {
    const navigator = useNavigate()
    let loection = useLocation();
    const logout = (e) => {
        e.preventDefault();
        // preventDefault
        localStorage.removeItem("usertoken");
        navigator("/login");
    }
    return (
        <>
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                <div className="container-fluid">
                    <Link className="navbar-brand" to="/">inotebook</Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <Link className={`nav-link ${loection.pathname === "/" ? "active" : ""}`} aria-current="page" to="/">Home</Link>
                            </li>
                            <li className="nav-item">
                                <Link className={`nav-link ${loection.pathname === "/about" ? "active" : ""}`} to="/about">About</Link>
                            </li>
                        </ul>
                    </div>
                </div>
                <div className="collapse navbar-collapse" id="navbarNav">
                    {!localStorage.getItem("usertoken") ? <ul className="navbar-nav" >
                        <li className="nav-item">
                            <Link className={`nav-link ${loection.pathname === "/login" ? "active" : ""}`} to="/login">Login</Link>
                        </li>
                        <li className="nav-item">
                            <Link className={`nav-link ${loection.pathname === "/signup" ? "active" : ""}`} aria-current="page" to="/signup">SignUp</Link>
                        </li>
                    </ul> : <button type="button" onClick={logout} className="btn btn-primary mx-3">Logout</button >}
                </div>
            </nav>
        </>
    )
}

export default Navbar;