import React, { useContext } from 'react';
import notes from '../context/notes/noteContext';

function NoteItem(props) {
    const noteContext = useContext(notes);
    const { deleteNote } = noteContext;
    return (
        <>
        
        <div className='col-3 mb-3'>
            <div className="card ">
                <div className="card-body">
                    <h5 className="card-title">{props.note.title}</h5>
                    <p className="card-text">{props.note.description}</p>
                    <button type="button" className="btn btn-primary" data-bs-toggle="modal" data-bs-target="#exampleModal" data-bs-whatever="@fat" onClick={() => { props.updateNote(props.note._id) }}>Update</button>
                    <button type="button" className="btn btn-dark m-3" onClick={() => {
                       if(window.confirm(`Delete the item ${props.note.title}`)){
                           (deleteNote(props.note._id))
                           (props.ShowAlert("Note Delete success", "info"))
                        }
                    }}>Delete</button>
                </div>
            </div>
        </div>
        </>

    )
}

export default NoteItem