import React,{useEffect} from 'react'
import GoogleLogin from 'react-google-login'
import { gapi } from 'gapi-script';
import { useNavigate } from 'react-router-dom';
import URL from './Host';



const Googlelogin = () => {
  const Navigate = useNavigate()
  useEffect(() => {
    function start() {
      gapi.client.init({
        clientId: process.env.REACT_APP_GOOGLE_CLIENT_ID,
        scope: ""
      })
    }
    gapi.load('client:auth2', start)
  });
    const SuccessLogin =async (googleData) =>{
        // console.log(googleData)
        let result = await fetch(`${URL}/api/auths/google-login`,{
          method:'post',
          body:JSON.stringify({tokenId:googleData.tokenId}),
          headers:{
            'Content-Type':'application/json'
          }
        })
        result = await result.json()
        localStorage.setItem('usertoken', result.usertoken)
        setTimeout(() => {
          Navigate("/");
        }, 1000);
    }
    const FailureLogin = (error) =>{
        console.log(error)
        
    }
  return (
    <div>
        <GoogleLogin
        clientId={process.env.REACT_APP_GOOGLE_CLIENT_ID}
        buttonText='Login with Google'
        onSuccess={SuccessLogin}
        onFailure={FailureLogin}
        cookiePolicy={'single_host_origin'}
        />
    </div>
  )
}

export default Googlelogin