import React, { useContext, useState } from 'react'
import notes from '../context/notes/noteContext';


export const AddNotes = (props) => {
    const noteContext = useContext(notes);
    const { AddNotes } = noteContext;
    const [note, setNote] = useState({ description: "", title: "", tag: "" });
    const submitNote = (e) => {
        e.preventDefault();
        AddNotes(note.title, note.description, note.tag);
        document.getElementById("addreset").reset();
        if(note.title.length > 4 && note.description.length > 4 && note.tag.length > 3){
            props.ShowAlert("Note Added success", "info")
        }
        
        setNote({ description: "", title: "", tag: "" })
    }

    const inputchang = (e) => {
        setNote({ ...note, [e.target.name]: e.target.value });

    }
    return (
        <div>
            <form id='addreset' className='my-3 p-3' style={{ "border": "2px solid red" }}>
                <h1 className='row justify-content-center'>Add Notes</h1>
                <div>
                    <div className="form-floating mb-3">
                        <input type="text" className="form-control" onChange={inputchang} name='title' id="title" placeholder="title" />
                        <label htmlFor="floatingInput">Title</label>
                    </div>
                    <div className="form-floating">
                        <input type="text" className="form-control" name='description' onChange={inputchang} id="description" placeholder="description" />
                        <label htmlFor="floatingPassword">Description</label>
                    </div>
                    <div className="form-floating my-3">
                        <input type="text" className="form-control" name='tag' onChange={inputchang} id="tag" placeholder="tag" />
                        <label htmlFor="floatingPassword">Tag</label>
                    </div>
                    <div className="form-floating my-3">
                        <button type="button" className="btn btn-info" onClick={submitNote}>Add Notes</button>
                    </div>
                </div>
            </form>
        </div>
    )
}
