import React, { useState } from "react";
import NoteContext from "./noteContext";
import host from "../../component/Host";



const NoteState = (props) => {
  
  ;
  const [notes, setNotes] = useState([]);
  let allnotes = async () => {
    let response = await fetch(`${host}/api/notes/fetchnotes`, {
      method: "GET",
      headers: {
        'Content-Type': 'application/json',
        'user-token': localStorage.getItem("usertoken")
      },
    })
    let note = await response.json()
    setNotes(note);
  };

  // add note
  const AddNotes = async (title, description, tag) => {
    if (title.length > 4 || description.length > 4 || tag.length > 3) {
      let response = await fetch(`${host}/api/notes/createnotes`, {
        method: "POST",
        headers: {
          'Content-Type': 'application/json',
          'user-token': localStorage.getItem("usertoken")
        },
        body: JSON.stringify({ title, description, tag })
      })
      let note = await response.json()
      if (note.errors) {
        alert("all field requerd")
      } else {
        setNotes(notes.concat(note))
      }
    } else {
      alert("all field requerd lesh then 5 cherecherater")

    }
    //  console.log(note.errors)
    // console.log(note)
  }

  // delete note

  const deleteNote = async (id) => {
     await fetch(`${host}/api/notes/deletenote/${id}`, {
      method: "DELETE",
      headers: {
        'Content-Type': 'application/json',
        'user-token': localStorage.getItem("usertoken")
      },
    })
    allnotes();

  }
  const [unote, setUnote] = useState([]);
  const updateNote = (id) => {
    for (let index = 0; index < notes.length; index++) {
      let element = notes[index]
      if (element._id === id) {
        // console.log(element)
        // let title = element.title;
        // let description = element.description;
        // let tag = element.tag;
        setUnote(element);
      }
    }
  }



  const updateNoteapi = async (id, title, description, tag) => {
      await fetch(`${host}/api/notes/updatenode/${id}`, {
        method: "PUT",
        headers: {
          'Content-Type': 'application/json',
          'user-token': localStorage.getItem("usertoken")
        },
        body: JSON.stringify({ title, description, tag })
      })
      allnotes();
    
  }


  return (
    <NoteContext.Provider value={{ notes, AddNotes, deleteNote, allnotes, updateNote, unote, updateNoteapi }}>

      {props.children}

    </NoteContext.Provider>
  )
}

export default NoteState;
