import React, { useContext, useEffect } from 'react'
import { useNavigate } from 'react-router-dom';
import note from '../context/notes/noteContext';
import { AddNotes } from './AddNotes';
import Modal from './Modal';
import NoteItem from './NoteItem';

function Notes(props) {
    let Navigate = useNavigate();
    const noteContext = useContext(note);
    const { notes, allnotes, unote, updateNote } = noteContext;
    useEffect(() => {
        // console.log(usertoken);
        if (localStorage.getItem("usertoken")) {
            allnotes()
            props.ShowAlert("Welcome to react inotebooks" ,"info")
        } else {
            Navigate("/login")
        }
        // eslint-disable-next-line
    }, []);
    return (
        <>
            <Modal unote={unote} ShowAlert = {props.ShowAlert} />
            <AddNotes ShowAlert = {props.ShowAlert}/>
            <div>
                <div className='container p-2' style={{ "border": "2px solid green" }}>
                    <h1 className='text-center'>Your Notes</h1>
                    <h3>{notes.length === 0 && "No Notes found"}</h3>
                    <div className='row' >
                        {notes.map((notes) => {
                            return (
                                <NoteItem note={notes} key={notes._id} updateNote={updateNote} ShowAlert = {props.ShowAlert} />
                            )
                        })}
                    </div>
                </div>
            </div>
        </>
    )
}

export default Notes