
import React, { useState } from 'react'
import { useNavigate } from "react-router-dom";
import Googlelogin from './Googlelogin';
import URL from './Host';


const SignUp = (props) => {
  let Navigate = useNavigate();
  const [signup, setsignup] = useState({ name: "", email: "", password: "" });
  const SignUp = async (e) => {
    e.preventDefault()
    if (signup.email.length > 0 && signup.password.length > 4) {
      let createuser = await fetch(`${URL}/api/auths/createuser`, {
        method: "POST",
        body: JSON.stringify({ name: signup.name, password: signup.password, email: signup.email }),
        headers: {
          "Content-Type": "application/json"
        }
      })
      createuser = await createuser.json();
      console.log(createuser)

      // console.log(usersignup.errors[0].msg)
      if (createuser.errors) {
        alert(createuser.errors[0].msg)
      }
      if (createuser.err) {
        alert(createuser.err)
      }
      if (createuser.usertoken) {
        localStorage.setItem('usertoken', createuser.usertoken)
        props.ShowAlert("Signup success", "info")
        setTimeout(() => {
          Navigate("/");
        }, 3000);
      }
    } else {
      alert("Plases enter email id and password less then 5 character");
    }
  }


  const inputchange = (e) => {
    console.log({ ...signup, [e.target.name]: e.target.value })
    setsignup({ ...signup, [e.target.name]: e.target.value });
  }

  return (
    <div className='container'>
      <form>
        <div className="mb-3">
          <label htmlFor="name" className="form-label">Name</label>
          <input type="text" name='name' className="form-control" id="name" onChange={inputchange} aria-describedby="emailHelp" />
        </div>
        <div className="mb-3">
          <label htmlFor="email" className="form-label">Email address</label>
          <input type="email" name='email' className="form-control" id="email" onChange={inputchange} aria-describedby="emailHelp" />
        </div>
        <div className="mb-3">
          <label htmlFor="password" className="form-label">Password</label>
          <input type="password" name='password' className="form-control" onChange={inputchange} id="password" />
        </div>
        <button type="button" className="btn btn-primary" onClick={SignUp}>Submit</button>
      </form>
      <div className='my-3'>
        <Googlelogin />
      </div>
    </div>

  )
}

export default SignUp;