import React, { useContext, useState, useRef } from 'react';
import notes from '../context/notes/noteContext';

const Modal = (props) => {
  const noteContext = useContext(notes);
  const { updateNoteapi } = noteContext;
  let ref = useRef(null);


  const [note, setNote] = useState({ description: props.unote.description, title: props.unote.title, tag: props.unote.tag });
  const submitNote = (e) => {

    let id = props.unote._id;
    //  let title = document.getElementById("title").value
    //  let description = document.getElementById("description").value
    //  let tag = document.getElementById("tag").value
    updateNoteapi(id, note.title, note.description, note.tag);
    document.getElementById("reset").reset();
    ref.current.click();
    props.ShowAlert("Your note updated" ,"info")
  }

  const inputchang = (e) => {
    setNote({ ...note, [e.target.name]: e.target.value });
  }


  return (
    <div>
      <div className="modal fade" id="exampleModal" tabIndex={-1} aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">Update Notes</h5>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
            </div>
            <div className="modal-body">
              <form id='reset'>
                <div className="mb-3">
                  <label htmlFor="title" className="col-form-label">Title:</label>
                  <input type="text" className="form-control" id="title" name='title' onChange={inputchang} defaultValue={props.unote.title} />
                </div>
                <div className="mb-3">
                  <label htmlFor="description" className="col-form-label">Description:</label>
                  <input className="form-control" id="description" name='description' onChange={inputchang} defaultValue={props.unote.description} />
                </div>
                <div className="mb-3">
                  <label htmlFor="tag" className="col-form-label">Tag:</label>
                  <input className="form-control" id="tag" name='tag' onChange={inputchang} defaultValue={props.unote.tag} />
                </div>
              </form>
            </div>
            <div className="modal-footer">
              <button type="button" ref={ref} className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
              <button type="button" className="btn btn-primary" onClick={submitNote}>Update</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Modal;
