import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";


const About = () => {
    const Navigate = useNavigate()
    useEffect(() => {
        if (!localStorage.getItem('usertoken')) {
            Navigate('/')
        }
    });
    return (
        <>
            <h1 className="text-center"> This is About page</h1>
        </>
    )
}

export default About;