
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import Googlelogin from "./Googlelogin";
import URL from './Host';


const Login = (props) => {
  let Navigate = useNavigate();
  const [login, setLogin] = useState({ email: "", password: "" });
  const loginsubmit = async (e) => {
    e.preventDefault()
    if (login.email.length > 0 && login.password.length > 4) {
      let loginuser = await fetch(`${URL}/api/auths/login`, {
        method: "POST",
        body: JSON.stringify({ password: login.password, email: login.email }),
        headers: {
          "Content-Type": "application/json"
        }
      })
      let userlogin = await loginuser.json();

      // console.log(userlogin.errors[0].msg)
      if (userlogin.errors) {
        alert(userlogin.errors[0].msg)
      }
      if (userlogin.err) {
        alert(userlogin.err)
      }
      if (userlogin.usertoken) {
        localStorage.setItem('usertoken', userlogin.usertoken)
        props.ShowAlert("login success", "info")
        setTimeout(() => {
          Navigate("/");
        }, 3000);
      }
    } else {
      alert("Plases enter email id and password less then 5 character");
    }
  }


  const inputchange = (e) => {
    setLogin({ ...login, [e.target.name]: e.target.value });
  }

  return (
    <div className='container'>
      <form>
        <div className="mb-3">
          <label htmlFor="email" className="form-label">Email address</label>
          <input type="email" name='email' className="form-control" id="email" onChange={inputchange} aria-describedby="emailHelp" />
        </div>
        <div className="mb-3">
          <label htmlFor="password" className="form-label">Password</label>
          <input type="password" name='password' className="form-control" onChange={inputchange} id="password" />
        </div>
        <button type="button" className="btn btn-primary" onClick={loginsubmit}>Submit</button>
      </form>
      <div className='my-3'>
      <Googlelogin />
      </div>
    </div>

  )
}

export default Login;
